@mixin base() {
    background-color: $primary;
    padding: 2rem;

    @media screen and (min-width: 768px) {
        flex: 1 1 auto;
    }

    & + & {
        margin: 2rem 0 0 0;

        @media screen and (min-width: 768px) {
            margin: 0 0 0 2rem;
        }
    }
}

&__card {
    @include base;
}